.demo-button button {
  width: 160px;
  background: #00c7ae repeat scroll 0 0 !important;
  border: 0 none;
  border-radius: 50px;
  color: #ffffff;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 13px 20px;
  text-transform: capitalize;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  font-weight: 900;
}
.banner-global::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000 none repeat scroll 0 0;
  opacity: 0.7;
}
.banner-global {
  height: 100vh !important;
  margin: 0 !important;
  padding: 0 !important;

  background-size: cover;
}
.banner-text {
  z-index: 100 !important;
}
.banner-text h3 {
  padding-top: 6rem;
  margin-bottom: 40px;
  font-size: 48px;
  font-weight: 700;
  color: #ffffff;
}
.banner-text p {
  font-size: 16px;
  color: #ffffff;
}
