html {
  scroll-behavior: smooth;
}
p {
  color: #747b8c;
}
h3 {
  color: #2e2e43;
}
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif !important;
}
.enroll-link {
  margin: 0 !important;
}
.nav {
  background-color: var(--off-white);
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}
.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.nav-item {
  display: inline;
  margin-left: 2rem;
  color: #333;
}
.nav-item:hover {
  color: black;
  cursor: pointer;
}
.section-dark {
  background: #333;
  color: white;
}
.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}
